<template>
  <div class="pc-header-wrapper">
    <div class="pc-nav-fixed-block">
      <div class="pc-navbar-main">
        <div class="pc-logo-block" @click="jumpRoutePage('index')">996</div>
        <el-menu
          mode="horizontal"
          :default-active="currentActive"
          background-color="#FFFFFF"
          active-text-color="#030047"
          text-color="#030047"
          class="head-nav-menu-block"
          @select="handleSelect"
        >
          <el-menu-item index="1">996</el-menu-item>
          <el-menu-item index="2">愿景</el-menu-item>
          <el-menu-item index="3">Mint</el-menu-item>
          <el-menu-item index="4">关于我们</el-menu-item>
        </el-menu>
        <div
          @click="connectWallet"
          class="connect-wallet-block"
          v-if="!walletConnectStatus.connected || !walletConnectStatus.isTargetNetwork"
        >
          {{ !walletConnectStatus.connected ? '连接钱包' : '切换网络' }}
        </div>
        <div class="address" v-else>{{ walletConnectStatus.address }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
  data() {
    return {
      currentActive: "2",
      navRouteNameConfig: ["index", "vision", "mint", "aboutUs"],
    };
  },

  computed: {
    ...mapState({
      walletConnectStatus: (state) => state.WalletConnect.walletConnectStatus,
    }),
  },

  mounted() {
    let routeName = this.$route.name;
    this.navRouteNameConfig.map((item, index) => {
      if (routeName === item) {
        this.currentActive = (index + 1).toString();
      }
    });
  },

  methods: {
    handleSelect(key) {
      let routeName = this.navRouteNameConfig[parseInt(key) - 1];
      if (this.$route.name !== routeName) {
        this.$router.push({
          name: routeName,
        });
      }
    },

    jumpRoutePage(routeName) {
      if (routeName === this.$route.name) {
        return;
      }

      this.$router.push({
        name: routeName,
      });
    },

    async connectWallet() {
      await window.dapp.enableBrowserExtension(window.networkId)
    },
  },
};
</script>

